.App, html {
  text-align: left;
  background-color: #282c34;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: space-evenly;
}

.title {
  align-items: flex-start;
  display: flex;
  margin-left: 30px;
}

.modal {
  background-color: rgba(35, 35, 51, 0.8);
  margin: 10%;
}

.row {
  flex-direction: row;
  justify-content: space-evenly;
  display: flex;
  background-color: #282c34;
}

.column {
  flex-direction: column;
  display: flex;
  margin-top: 20px;
  align-items: center;
  margin-bottom: 20px;
}

.facade-in-a-column, img {
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
}

.label {
  text-decoration: underline;
}

h1.page-header {
  margin: 0;
  padding: 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
